.hero__title {
  font-size: 70px;
  font-weight: 400;
  margin-bottom: 12px;
  margin-top: 0;
}

.hero__events-list {
  display: flex;
}

.events__item {
  font-size: 24px;
  line-height: 32px;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}

.events__item:not(:last-child) {
  width: 200px;
  height: 90px;
}

.events__item:first-child {
  font-size: 20px;
  text-transform: uppercase;
  text-align: center;
  color: black;
  background-color: white;
}

.events__item:last-child {
  width: 150px;
  height: 90px;
}

.hero__events-list::after {
  content: '';
  display: inline-block;
  height: 90px;
  width: 2px;
  margin-left: -150px;
  background-color: white;
}
