.registration {
  min-height: 100vh;
  overflow: hidden;
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
  color: white;
  background:
    linear-gradient(90deg, #000 0%, rgba(0, 0, 0, 0.2) 100%),
    url('../../assets/background/mainBg1.png') center/cover fixed;
  background-size: cover;
}

.registration__container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.registration__logo {
  width: 100px;
  height: 25px;
  margin-top: 70px;
  margin-left: 320px;
}

.registration__form {
  width: 550px;
  height: 50vh;
  display: flex;
  flex-direction: column;
}

input {
  width: 100%;
  height: 50px;
  padding: 5px;
  color: #f5f5f5;
  border: 1px solid #f5f5f5;
  background: rgba(0, 0, 0, 0.5);
}

input:not(:last-child) {
  margin-bottom: 30px;
}

.registration__btn {
  align-self: center;
  font-size: 20px;
  text-transform: uppercase;
  color: #f5f5f5;
  width: auto;
  cursor: pointer;
  display: inline-block;
  position: relative;
}

.registration__btn::after {
  content: '';
  display: block;
  width: 100%;
  height: 1px;
  background: #f5f5f5;
  position: absolute;
  bottom: 0;
  left: 0;
}

.registration__btn:hover::after {
  transition: 0.3s;
  opacity: 0;
}

.registration__buttons {
  width: 550px;
  margin-bottom: 30px;
}

.registration__sign {
  font-size: 20px;
  color: #000;
  width: 108px;
  height: 40px;
  margin-right: 15px;
  padding: 8px 30px;
  border: 1px solid #f5f5f5;
  background-color: #f5f5f5;
}

.registration__reg {
  font-size: 20px;
  overflow: hidden;
  color: #000;
  width: 200px;
  height: 40px;
  padding: 8px 30px;
  border: 1px solid #f5f5f5;
  background-color: #f5f5f5;
}

.registration__sign.active,
.registration__reg.active {
  background: rgba(0, 0, 0, 0);
  color: #f5f5f5;
}
