@font-face {
  font-family: Rubik;
  src: url('../fonts/Rubik-Regular.woff') format('woff');
  font-display: swap;
  font-weight: 400;
}

@font-face {
  font-family: Rubik;
  src: url('../fonts/Rubik-Bold.woff') format('woff');
  font-display: swap;
  font-weight: 700;
}

@font-face {
  font-family: Rubik;
  src: url('../fonts/Rubik-Medium.woff') format('woff');
  font-display: swap;
  font-weight: 500;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  padding: 0;
}

p {
  margin: 0;
  padding: 0;
}

ul,
ol {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: inherit;
  text-decoration: none;
}

img {
  max-width: 100%;
}

body {
  min-height: 100vh;
  overflow: hidden;
  font-family: 'Rubik', sans-serif;
  margin: 0;
  padding: 0;
  color: white;
}

.container {
  max-width: 1920px;
  max-height: 100%;
  margin: 0 auto;
  padding: 0 320px;
}

.hero {
  height: 100vh;
}

.hero__container {
  height: 100%;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  list-style: none;
}

.profile__btn {
  font-size: 20px;
  text-align: center;
  width: 170px;
  height: 40px;
  padding: 8px 50px;
  margin-bottom: 10px;
  background-color: white;
}

.hero__container {
  display: flex;
  justify-content: space-between;
}
