.sidebar {
  position: absolute;
  left: 0;
  top: 0;
  padding: 70px 50px;
  width: 11vw;
  height: 100vh;
  transition: left 0.3s ease-in-out;
}

.sidebar.open .menu-list__item {
  transform: translateX(0);
  opacity: 1;
}

.sidebar.open .menu-list {
  opacity: 1;
}

.sidebar__btn {
  display: flex;
  justify-content: space-between;
  width: 86px;
  height: 19px;
  text-transform: uppercase;
  color: rgb(143, 150, 150);
  transition: 0.3s;
}

.menu-icon {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-top: 3px;
}

.cross-line {
  background-color: #f5f4f0;
  width: 20px;
  height: 2px;
  margin: 2px;
  transition: transform 0.3s ease-in-out;
}

.cross-line:first-child {
  transform: rotate(0deg);
}

.cross-line:last-child {
  transform: rotate(0deg);
}

.cross-line.open:first-child {
  transform: rotate(45deg) translateX(4px);
}

.cross-line.open:last-child {
  transform: rotate(-45deg) translateX(4px);
}

.sidebar__btn:hover {
  color: #f5f4f0;
}

.menu-list {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  opacity: 0;
  transition: opacity 0.3s ease-in-out;
}

.menu-list__item {
  display: flex;
  align-items: center;
  color: rgb(143, 150, 150);
  font-size: 16px;
  text-transform: uppercase;
  transition: 0.3s;
  transform: translateX(-20px);
  opacity: 0;
}

.menu-list__item:hover {
  color: #f5f4f0;
}

.menu-list__item:not(:last-child) {
  margin-bottom: 50px;
}
